*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-family-main: "Manrope", sans-serif;
  --bgc-main: #fff;
  --bgc-second: #e8edf6;
  --bgc-accent: #1e2eb8;
  --bgc-stroke: #bbc6da;
  --bgc-lavender: #c9cef6;
  --bgc-black: #000;
  --cl-txt-main: #131313;
  --cl-txt-second: #545454;
  --cl-txt-accent: #1e2eb8;
  --cl-txt-white: #fff;
  --cl-txt-red: #eb1515;
  --cl-txt-grey: #7988a3;
  --cl-txt-grey-light: #d9d9d9;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --fast-anim: 200ms ease;
  --slow-anim: 350ms ease-in-out;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

.hidden {
  display: none;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  font-family: var(--font-family-main);
  letter-spacing: .04em;
  min-height: 100vh;
  color: var(--cl-txt-main);
  background-color: var(--bgc-main);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  position: relative;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

@keyframes flowerAnim {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(90deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(270deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes fadeInRightAnim {
  0% {
    opacity: .5;
    transform: translateX(50px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInTopAnim {
  0% {
    opacity: 1;
    transform: translateY(70%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes changeBgcAnim {
  0% {
    background-color: var(--bgc-accent);
  }

  50% {
    background-color: var(--bgc-lavender);
  }

  100% {
    background-color: var(--bgc-second);
  }
}

@keyframes scaleAnimation {
  0% {
    opacity: .5;
    transform: scale(.5);
  }

  25% {
    opacity: .7;
    transform: scale(.6);
  }

  50% {
    opacity: .6;
    transform: scale(.7);
  }

  75% {
    opacity: .8;
    transform: scale(.8);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

