.visually-hidden {
  position: absolute;
  white-space: nowrap;
  margin: -1px;

  width: 1px;
  height: 1px;

  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

.is-hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

.hidden {
  display: none;
}