@keyframes flowerAnim {
    0% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(90deg);
    }

    50% {
        transform: rotate(180deg);
    }

    75% {
        transform: rotate(270deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes fadeInRightAnim {
    0% {
        transform: translateX(50px);
        opacity: 0.5;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeInTopAnim {
    0% {
        transform: translateY(70%);
        opacity: 1;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes changeBgcAnim {
    0% {
        background-color: var(--bgc-accent);
    }

    50% {
        background-color: var(--bgc-lavender);
    }

    100% {
        background-color: var(--bgc-second);
    }
}

@keyframes scaleAnimation {
    0% {
        transform: scale(0.5);
        opacity: 0.5;
    }

    25% {
        transform: scale(0.6);
        opacity: 0.7;
    }

    50% {
        transform: scale(0.7);
        opacity: 0.6;
    }

    75% {
        transform: scale(0.8);
        opacity: 0.8;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}